.section {
    padding: 8px;
}

.header {
    text-transform: uppercase;
    font-size: 12px;
    color: #888;
    font-weight: bold;
    padding: 8px 16px;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 8px 16px;
}

.row.right {
    justify-content: flex-end;
}

.text {
    padding: 8px;
    border: 2px solid black;
    border-radius: 2px;
    font-size: 16px;
    flex-grow: 1;
    margin: 4px 16px;
    outline: none;
}

.button {
    font-size: 16px;
    text-transform: uppercase;
    font-weight: bold;
    padding: 8px 16px;
    cursor: pointer;
    margin: 0 8px;
    border-radius: 2px;
}

.button:hover {
    background-color: rgba(0, 0, 0, 0.08);
}

.button.primary {
    background-color: #4D90FE;
    color: white;
}

.button.primary:hover {
    background-color: #357AE8;
}
