.root {
    width: 250px;
}

.header {
    text-transform: uppercase;
    font-size: 12px;
    color: #888;
    font-weight: bold;
    padding: 8px 16px;
}

.item {
    padding: 8px 16px;
    cursor: pointer;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
}

.item:hover {
    background-color: rgba(0, 0, 0, 0.04);
}
