.root {
    width: 100%;
}

.table {
    display: table;
    width: 100%;
    margin: 0 12px 0 24px;
}

.header {
    display: table-row;
    color: rgba(0, 0, 0, 0.64);
}

.header > .element {
    border-bottom: 1px solid rgba(0, 0, 0, 0.24);
}

.body {
    display: table-row-group;
    overflow-y: scroll;
}

.row {
    display: table-row;
}

.row:hover {
    background-color: rgba(0, 0, 0, 0.04);
    cursor: default;
}

.selected {
    background-color: rgba(0, 0, 0, 0.08);
}

.element {
    display: table-cell;
    vertical-align: middle;
    padding: 8px 16px;
}
