.root {
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.40);
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.root > * {
    margin: auto;
    border-radius: 4px;
    background-color: white;
    max-height: 100%;
    overflow-y: scroll;
    width: 800px;
}

@media screen and (max-width: 900px) {
    .root > * {
        border-radius: 0;
        width: 100%;
        height: 100%;
    }
}
